import { PAGE_SIZE } from '@/assets/config'
import { CODE_TREE } from '@/assets/constant'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Service from '@/assets/service/service'
export default {
  name: 'customForm',
  data () {
    return {
      CodeTree: CODE_TREE,
      codes: [],
      items: [],
      form: {},
      total: 0,
      page: 1,
      pageSize: PAGE_SIZE,
      condition: {},
      searchOptions: [
        { label: '发表时间', key: 'publishing_time', type: 'date' },
        { label: '名称', key: 'name' },
        { label: '作者', key: 'author' }
      ]
    }
  },
  methods: {
    details (item) {
      tabsService.addDetailsTab('/views/customFormDetails?code=' + item.code, '自定义字段-' + item.name)
    },
    getDataTables () {
      this.CodeTree.map(tree0 => {
        if (this.form[tree0.permission] == 1) {

          tree0.children.map(tree1 => {
            if (tree1.children) {
              tree1.children.map(tree2 => {
                if (!tree2.isDisable) {
                  this.codes.push({
                    name: tree2.name,
                    code: tree2.code,
                  })
                }
              })
            } else {
              if (!tree1.isDisable) {
                this.codes.push({
                  name: tree1.name,
                  code: tree1.code,
                })
              }
            }
          })

        }
      })
    }
  },
  created () {
    Service.PageSet.get().then(response => {
      this.form = response.data
      this.getDataTables()
    })
  },
  mounted () {
  }
}



